import React from 'react'

import Icon from '../Icon/Icon'
import Button from '../Buttons/Button'
import Popover from '../Popover/Popover'

import styles from './ColumnHeader.module.scss'

const ColumnHeader = ({ label, popover, sort }) => {
	return (
		<div
			role='columnheader'
			className={styles['columnheader']}>
			<div className={styles['columnheader-info']}>
				<span>{label}</span>
				{
					popover && (
						<Popover className={styles['columnheader-popover']} popover={popover} />
					)
				}
			</div>

			{
				sort && (
					<Button
						className={styles['columnheader-sort']}>
						<Icon
							className={styles['columnheader-sort-icon']}
							name={'arrowDown'} />
						<Icon
							className={styles['columnheader-sort-icon']}
							name={'arrowDown'} />
					</Button>
				)
			}
		</div>
	)
}

export default ColumnHeader